.input {
  box-sizing: border-box;
  width: auto;
  min-width: 200px;
  padding: 10px;
  border: 1px solid var(--grey-light);
  border-radius: 4px;
  font-family: var(--os);
  font-size: var(--fz);
  line-height: calc(var(--fz) + 4px);
  background: var(--white);
  display: block;
  outline: none;
  color: var(--grey-dark);
  &[disabled] {
    opacity: 0.5;
    pointer-events: none;
    + label {
      opacity: 0.75;
      pointer-events: none;
    }
  }
  &::placeholder {
    color: var(--grey-dark);
    font-size: var(--fz);
  }
  &:hover {
    color: var(--black);
    border-color: var(--black);
    ::placeholder {
      color: var(--black);
    }
  }
  &:focus {
    border-color: var(--accent);
  }
  &:active {
    border-color: var(--accent);
    border-width: 0;
    outline: 2px solid var(--accent);
  }
  &.error {
    border-color: var(--danger);
  }
}

.input-holder {
  position: relative;
  display: inline-block;
  vertical-align: top;
  label {
    cursor: pointer;
    display: inline-block;
    color: var(--grey-black);
    text-transform: uppercase;
    background: #fff;
    padding: 2px 4px;
    top: 0;
    left: 8px;
    transform: translateY(-50%);
    background: var(--white);
    font-size: calc(var(--fz) - 4px);
    line-height: calc(var(--fz) - 2px);
    position: absolute;
    z-index: 1;
  }
}
